import React from 'react'
import './index.css'


class YearCategoryElement extends React.Component {

  state = {
    clicked: false
  }

  render(){

    return(
      <div className="YearCategoryElement" onClick={()=>this.props.func(this.props)}>
        <div className="Year" >{this.props.year}</div>
        <div className='Category'>{this.props.category}</div>
      </div>
    )
  }
}

class Element extends React.Component {

  state = {
    collapsed: false,
  }

  changeCollapsed = () => this.setState({collapsed: !this.state.collapsed})

  render(){

    let params = {
      marker1: '+',
      marker2: '-',
    }

    return(
      <div className="Element">
        <div style={{'display':'flex', 'justify-content': 'space-between'}}>
          <div className="Element_Title" onClick={()=>this.props.data ? this.changeCollapsed() : null} style={{'cursor': (this.props.data ? 'pointer' : 'default')}}>
            <div className="Element_Marker">{this.props.data ? (this.state.collapsed ? params.marker1 : params.marker2) : null}</div>
            <div className="Element_Text">{this.props.text}</div>
          </div>

        </div>
        <div className="Element_Data">
          {this.state.collapsed ? null : this.props.data}
        </div>
      </div>
    )
  }

}

let ListOfLaureates = ( data ) => {

  return (
    <div>
      <div className="ListOfLaureates_header">Laureates</div>
      <div>
        {data.laureates.map( (d, i) => {
          return(
            <div className='laureat' key={i}>
              <div className='name'>
                <div className="firstname">{d.firstname}</div>
                <div className="surname">{d.surname}</div>
              </div>
              <div className="motivation">{d.motivation}</div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

class App extends React.Component {
  constructor(props){
    super(props)
    this.get_data()
    this.changeSelected = (( newSelected ) => this.setState({selected: newSelected})).bind(this)
  }

  state = {
    data: {prizes: []},
    selected: {
      overallMotivation: 'Select',
      laureates: []
    }
  }

  get_data = () => {

      fetch('http://api.nobelprize.org/v1/prize.json')
    .then( ( response ) => {
      let text = response.text()
      return text
    })
    .then( ( text ) => {
      let json = JSON.parse( text )
      this.setState({data: json})
    })
    .catch( ( error ) => console.log( error ) )
    }

  render(){
    return(
      <div className="App">
        <div className="Laureates">
          <div>
            { this.state.data.prizes.map( ( d, i ) => {
              return <YearCategoryElement {...d } func={this.changeSelected} key={i}/>
            })}
          </div>
          <div className="TableInfo">
            <div>{this.state.selected.overallMotivation}</div>
            <div>
              <ListOfLaureates laureates={this.state.selected.laureates}/>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default App
